
html, body{height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
  text-align: left;
}
::-moz-selection {
  background: #d6288b;
  color: white;
}
::selection {
  background: #d6288b;
  color: white;
}
ul {
  padding-left: 0px;
}
li {
  padding-left: 10px;
  list-style-position: inside;
}
#app {
  position: relative;
}
.row {
  margin: 0;
}
.first {
  margin-top: 20px;
}
.page-footer {
  border-top: 1px solid #eee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.page-footer > p {
  text-align: center;
  line-height: 50px;
}
#fire {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -5;
}

